.App-logo {
  height: 46px;
  pointer-events: none;
  border-radius: 50%;
  position: absolute;
  margin: 0px;
  padding: 0px;
  top: 15px;
  left: 7px;
  margin-top: 10px;
  margin-left: 5px;
  background-color: rgb(8, 56, 247);
}

.tlet{
  margin-left: -4px;
  font-size: 27px;
}
b{
  color: rgb(110, 255, 27);
  font-size: 25px;
  font-weight: bold;
  letter-spacing: -1px;
}
.App-header {  
  height: 40px;
  margin: 0px;
  padding-top: 25px;
  padding-left: 80px;
  top: 0px;
  color: white;
  position: fixed;
  width: 100%;
  flex-direction: column;
  background-color: #0000ff;
  z-index: 1;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(190deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin 3s ;
    animation-timing-function: linear;
	  animation-iteration-count: 1;
    position: fixed;
    z-index: 1;
  }
}
a{
  text-decoration: none;
  color: white;
}
h4{
  margin: 0px;
  margin-left: 80px;
  padding-top: 14px;
  font-size: 20px;
}

.purple-wave {
  height: 11rem;
  background-repeat: no-repeat;
  justify-content: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background-image: url(backg.svg);
  background-size: 115rem 14rem;
  background-position:center;
  z-index: 1;
  
}

.logo-back{
  background-color: rgb(157, 252, 4);;
  height: 94px;
  width: 92px;
  position: absolute;
  top: 0px;
  border-bottom-right-radius: 53%;
  border-top-right-radius: 10%;
}

.hero-title{
  position: fixed;
  margin-top: 140px;
  margin-left: 300px;
}

.center{
  padding-top: 90px;
  text-align: center;
}