#header {
    position: fixed;
    width: 230px;
    border-radius: 30px;
    left: -3px;    
    top:120px;
  }

  h5{
    margin-top: 0px;
    padding: 20px;
  }
 
  #header .closemenu {
    color: rgb(0, 0, 0);
    position:fixed;
    left: 0px;
    top: 115px;
    background-color: rgba(247, 244, 244, 0);
    line-height: 0px;
    border-radius: 50%;
    text-decoration: none;
    font-weight: bold;
    font-size: 28px;
    cursor: pointer;
    padding: 20px;
  }

  #header .pro-sidebar {
    width:10em;
    min-width: 82%;
    max-height: 100%;
    position:sticky;
    float: left;
    color: #000;
    margin-top: 49px;
    background-color: rgba(255, 255, 255, 0.041);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;  
    border-radius: 9px;
    border: #000 0px solid;
    overflow: hidden;
  }

  #header .pro-sidebar.collapsed {
    width: 31%;
    min-width: 30.4%;
    color: #000;
    text-decoration: none;
  }
  #header .pro-sidebar-inner {
    background-color: rgba(255, 255, 255, 0);
  }
  #header .pro-sidebar-inner .pro-sidebar-layout {
    overflow: hidden;
    text-decoration: none;
    padding-left: 0px;
    margin-right: 0px;
  }
 
  #header .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item {
    color: rgb(2, 2, 2);
    text-decoration: none;
    font-weight: bold;
  }
  #header .pro-sidebar-layout .pro-icon-wrapper {
    background-color: rgb(255, 255, 255);
    color: rgb(14, 13, 13);
    border-radius: 50px;
  }

  .one{
    margin-top: 7px;
  }
  .two{
    margin-top: 16px;
  }
  
  .two:hover{
    background-image: linear-gradient( #3700ffdc , #0044ff 120%);
    border-radius: 9px;
    border-radius: 5px;
    transform: scale(1.09);
    transition: 0.3s;
    color: white;
  }
  .one:hover{
    background-image: linear-gradient( #3700ff , #0044ff 120%);
    border-radius: 5px;
    border-radius: 5px;
    transform: scale(1.09);
    transition: 0.3s;
    color: rgb(7, 7, 7);
  }

  #header .pro-sidebar-inner .pro-sidebar-layout .active {
    background-image:  linear-gradient( #0000ff , #0044ff 120%);
    border-radius: 6px;
    color: rgb(255, 255, 255);
    font-size: 17px;
  }
  
  #header .logo {
    padding: 0px;
  }
  
  .dark-mode-btn{
   border: none;
   background-color:rgb(255, 255, 255);
   margin: 10px;
   color: #000000;
   border-radius: 50%;
   padding-top: 3px;
   padding: 10px 10px 8px 10px;
  }

  .container{
    margin: 45px 75px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    width: 30%;
    text-align: center; 
    border: #030303 0.2px solid;
  }

  
#button-5 {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  color: #000;
}

#button-5 a {
  position: relative;
  transition: all .45s ease-Out;
  color: #000;
}

#translate {
  transform: rotate(50deg);
  width: 93%;
  height: 300%;
  left: -200px;
  top: -30px;
  background: #0044ff;
  position: absolute;
  transition: all .2s ease-Out;
}

#button-5:hover #translate {
  left: 0;
}

#button-5:hover a {
  color: #ffffff;
}

  

